$header-height: 96px;

.header {
  width: 100%;
  height: 96px;
  background: var(--light-100);
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  box-shadow: 0px 0px 2px rgba(40, 41, 61, 0.04), 0px 4px 8px rgba(96, 97, 112, 0.16);;
}

.header__content {
  flex: 1 0;
  max-width: var(--container-width);
  display: flex;
  align-items: center;
}

.header__logo {
  display: block;
  border-radius: 50%;
  background: var(--light-500);
  width: 48px;
  height: 48px;
}

.header__menu-container {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.header-menu {
  margin: 0 20px 0 0;
  list-style: none;
  display: flex;
  padding: 0;
  font-weight: var(--medium-weight);
  font-size: 16px;
}

.header-menu__link {
  list-style: none;
  display: flex;
  cursor: pointer;

  &:hover {
    color: var(--dark-400);
  }

  &:not(:first-child) {
    margin-left: 40px;
  }
}

.header__profile-menu {
  margin-left: auto;
  display: flex;
}


